import * as React from "react";
import { Box, Typography } from "@mui/material";
import {
  Timeline as MUI_Timeline,
  TimelineItem as MUI_TimelineItem,
  TimelineSeparator,
  TimelineConnector,
  TimelineContent,
  TimelineDot,
  TimelineOppositeContent
} from "@mui/lab";
import { DenteLogoIcon } from "./icons";
import { styled } from "@mui/material/styles";

const VerticalTimelineOppositeContent = styled(
  TimelineOppositeContent
)(({ theme }) => ({}));

const VerticalTimelineSeparator = styled(TimelineSeparator)(({ theme }) => ({
  marginLeft: theme.spacing(2),
  marginRight: theme.spacing(2),
  minHeight: theme.spacing(12.5)
}));

const VerticalTimeline = styled(MUI_Timeline)(({ theme }) => ({
  margin: 0
}));

const VerticalTimelineItem = styled(MUI_TimelineItem)(({ theme }) => ({
  "&:before": {
    content: "none"
  }
}));

const VerticalTimelineConnector = styled(TimelineConnector)(({ theme }) => ({
  backgroundColor: theme.palette.primary.main
}));

const VerticalTimelineDot = styled(TimelineDot)(({ theme }) => ({
  borderColor: theme.palette.primary.main
}));

const VerticalTimelineContent = styled(TimelineContent)(({ theme }) => ({
  paddingLeft: theme.spacing(4)
}));

const TimelineItem = ({ isLast, date, title, description }) => {
  return (
    <VerticalTimelineItem>
      <VerticalTimelineOppositeContent>
        <Typography sx={{ color: "secondary.muted" }}>{date}</Typography>
      </VerticalTimelineOppositeContent>
      <VerticalTimelineSeparator sx={{ minHeight: isLast ? 4 : null }}>
        <VerticalTimelineDot
          variant="outlined"
          sx={{ color: "primary.main", borderColor: "secondary.main" }}
        >
          <DenteLogoIcon sx={{ p: 0.5 }} />
        </VerticalTimelineDot>
        {!isLast && <VerticalTimelineConnector />}
      </VerticalTimelineSeparator>
      <VerticalTimelineContent>
        <Typography
          variant="subtitle1"
          sx={{ color: "primary.main", fontWeight: "bold" }}
        >
          {title}
        </Typography>
        <Typography variant="body1" sx={{ textAlign: "justify" }}>
          {description}
        </Typography>
      </VerticalTimelineContent>
    </VerticalTimelineItem>
  );
};

const Timeline = ({ events }) => {
  return (
    <Box sx={{ width: "100%" }}>
      <VerticalTimeline>
        {events.map((event, index) => {
          return (
            <TimelineItem
              key={index}
              isLast={index + 1 === events.length}
              {...event}
            />
          );
        })}
      </VerticalTimeline>
    </Box>
  );
};

export default Timeline;
