import * as React from "react";
import { Box } from "@mui/material";

const VideoPlayer = ({
  src,
  controls,
  width,
  height,
  wrapperProps,
  videoProps
}) => {
  return (
    <Box sx={{ position: "relative", width, height }} {...(wrapperProps || {})}>
      <video
        src={src}
        controls={controls}
        width="100%"
        height="100%"
        {...(videoProps || {})}
      />
    </Box>
  );
};

export default VideoPlayer;
